import { configure, observable } from 'mobx'

configure({ enforceActions: true });

export class CommonStore {
    get(id) {
        return this[id];
    }
    set(id) {
        debugger;
    }
    @observable common = {
        userName: 'dingyoujian'
    } //公共store
}



